export const getListings = `query GetListings($hostId: String!) {
  getListings(hostId: $hostId) {
    hostId
    listingId
    listingName
    listingUrl
    paymentStatus
    registeredOn
    lastUpdateOn
  }
}
`

export const createListing = `mutation CreateListing($hostId: String!, $listingId: String!, $listingName: String, $listingUrl: String, $paymentStatus: String,  $registeredOn: AWSDate, $lastUpdateOn: AWSDateTime) {
  createListing(input: { hostId: $hostId, listingId: $listingId, listingName: $listingName, listingUrl: $listingUrl, paymentStatus: $paymentStatus, registeredOn: $registeredOn, lastUpdateOn: $lastUpdateOn })
  {
    hostId
    listingId
    listingName
    listingUrl
    paymentStatus
    registeredOn
    lastUpdateOn
  }
}
`

export const updateListing = `mutation UpdateListing($hostId: String!, $listingId: String!, $listingName: String, $listingUrl: String, $paymentStatus: String,  $registeredOn: AWSDate, $lastUpdateOn: AWSDateTime, $expectedLastUpdateOn: AWSDateTime!) {
  updateListing(input: { hostId: $hostId, listingId: $listingId, listingName: $listingName, listingUrl: $listingUrl, paymentStatus: $paymentStatus, registeredOn: $registeredOn, lastUpdateOn: $lastUpdateOn }, expectedLastUpdateOn: $expectedLastUpdateOn)
  {
    hostId
    listingId
    listingName
    listingUrl
    paymentStatus
    registeredOn
    lastUpdateOn
  }
}
`

