import store from '@/store'

// function timeout(ms) {
//     return new Promise(resolve => setTimeout(resolve, ms))
// }

const subscribe = (retrying = 0, topic) => {
  setTimeout(async () => {
    // console.log('local.loading', local.loading)
    
    local.loading = true

    // await timeout(3000)

    await store.dispatch('host/' + local.initializerKey).catch(e => console.log(e))

    const observable = await store.dispatch('host/' + topic)

    subscriptions[topic] = observable.subscribe({
      next: (eventData) => {
        retrying = 0
        console.log('eventData', eventData)
        store.dispatch('host/' + local.callbackKey, eventData.value.data[topic])
      },
      error: (data) => {
        console.error('subscribe connection errors', data)
        const { errors } = data
        if (errors && errors.find(error => error.message === 'Network Error')) {
          if (retrying === 0) {
            retrying = 50
          }

          subscribe(retrying * 2, topic)
        }
      },
      complete: (data) => console.log('completed', data)
    })

    local.loading = false


  }, retrying)
  
}

const disconnect = (topic) => {
  console.log(`disconnect ${topic}:`, subscriptions[topic])

  if (subscriptions[topic] && typeof subscriptions[topic].unsubscribe === 'function') {
    // console.log('before disconnect subscriptions', subscriptions)
    
    subscriptions[topic].unsubscribe()
    delete subscriptions[topic]

    // console.log('after disconnect subscriptions', subscriptions)
  }
}

const connect = (observableKey, initializerKey, callbackKey) => {
  local.initializerKey = initializerKey
  local.callbackKey = callbackKey

  if (local.loading) {
    console.log('My !!!connect local.loading', local.loading)
    return
  }

  let topic = observableKey

  if (!subscriptions[topic] || subscriptions[topic].closed) {
    disconnect(topic)
    subscribe(0, topic)
  }
}

const local = {
  initializerKey: null,
  callbackKey: null,
  loading: false
}


const subscriber = {
  connect,
  disconnect,
}

const subscriptions = {}

export default subscriber